import React, { createContext, useContext, useState } from 'react';

const PlayersContext = createContext();

export function PlayersProvider({ children }) {
  const [players, setPlayers] = useState([]);

  const addPlayer = (name, score) => {
    setPlayers([...players, { id: Date.now().toString(), name, score }]);
  };

  const removePlayer = (id) => {
    setPlayers(players.filter(player => player.id !== id));
  };

  const updatePlayerScore = (id, newScore) => {
    setPlayers(players.map(player => 
      player.id === id ? { ...player, score: newScore } : player
    ));
  };

  return (
    <PlayersContext.Provider value={{ players, addPlayer, removePlayer, updatePlayerScore }}>
      {children}
    </PlayersContext.Provider>
  );
}

export function usePlayers() {
  return useContext(PlayersContext);
} 